.canvas-container {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	overflow: hidden;
	margin: 50px;
	border-radius: 20px;
	border: 2px solid white;
}

.canvas-backdrop {
	position: absolute;
	background-color: black;
	width: 100%;
	height: 100%;
	z-index: -2;

	-webkit-transition: background-color 2s;
	-o-transition: background-color 2s;
	transition: background-color 2s;
}

#canvas {
	background-color: rgba(0,0,0,0);
}

.string-container {
	position: absolute;
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: end;
	    align-items: flex-end;
	pointer-events: none;
	font-size: 48px;
	background-color: rgba(0,0,0,0);
}

.string-text {
	background-color: rgba(256,256,256,0.5);;
	text-align: center;
}

.canvas-background-image {
	z-index: -1;
	position: absolute;
	top: 0;
	width: 1500px;
	height: 1500px;
	pointer-events: none;
	opacity: 0;
	border-radius: 20px;
	-webkit-transition: opacity 2s;
	-o-transition: opacity 2s;
	transition: opacity 2s;

}

.image-show {
	opacity: 1;
	-webkit-animation: 60s linear kenburns;
	        animation: 60s linear kenburns;
	-webkit-animation-iteration-count:infinite;
	        animation-iteration-count:infinite;
}

@-webkit-keyframes kenburns {
	0% {
		left: 0px;
		top: 0px;
	}

	25% {
		left: -500px;
		top: -500px;
	}

	50% {
		left: -500px;
		top: 0px;
	}

	75% {
		left: 0px;
		top: -500px;
	}

	100% {
		left: 0px;
		top: 0px;
	}
}

@keyframes kenburns {
	0% {
		left: 0px;
		top: 0px;
	}

	25% {
		left: -500px;
		top: -500px;
	}

	50% {
		left: -500px;
		top: 0px;
	}

	75% {
		left: 0px;
		top: -500px;
	}

	100% {
		left: 0px;
		top: 0px;
	}
}

.timer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	position: absolute;
	top: 10px;
	right: 10px;
	color: white;
	width: 200px;
	height: 50px;
	pointer-events: none;

	-webkit-transition: color 2s;
	-o-transition: color 2s;
	transition: color 2s;
}




